import {
  Box,
  Image,
  Badge,
  StatGroup,
  Stat,
  StatLabel,
  StatNumber,
  Link,
  Center,
  Text,
} from "@chakra-ui/react";
import coalMineImage from "../images/Coal_mine.png";

const MineInfo = (props) => {
  const data = props.mineInfo;

  if (!data) {
    return (
      <Center height="100%">
        <Text fontSize="sm" color="gray.600" textAlign="center">
          Click on a mine above to show more information
        </Text>
      </Center>
    );
  }

  return (
    <>
      <Center>
        <Image src={coalMineImage} width="80%"></Image>
      </Center>
      <Box>
        <Badge
          borderRadius="full"
          px="2"
          colorScheme={data.Status === "Operating" ? "green" : "gray"}
        >
          {data.Status}
        </Badge>
        <Box
          mt="1"
          fontWeight="semibold"
          as="h4"
          fontSize="14"
          lineHeight="tight"
          isTruncated
        >
          {data.Name}
        </Box>
      </Box>
      <StatGroup>
        <Stat marginTop="5px">
          <StatLabel color="gray.600">Distance from port</StatLabel>
          <StatNumber fontSize="14">
            {parseFloat(data.Distance).toFixed(2)} km
          </StatNumber>
        </Stat>
        <Stat marginTop="5px" marginLeft="10px">
          <StatLabel color="gray.600">Grade</StatLabel>
          <StatNumber fontSize="14">{data.Grade}</StatNumber>
        </Stat>
      </StatGroup>
      <StatGroup>
        <Stat marginTop="5px">
          <StatLabel color="gray.600">Annual Capacity</StatLabel>
          <StatNumber fontSize="14">{data.Output} Mt</StatNumber>
        </Stat>
        <Stat marginTop="5px" marginLeft="10px">
          <StatLabel color="gray.600">Find out more</StatLabel>
          <StatNumber fontSize="14">
            <Link color="teal.500" href={data.Wiki} isExternal>
              Global Energy Monitor
            </Link>
          </StatNumber>
        </Stat>
      </StatGroup>
    </>
  );
};

export default MineInfo;
