import { Button } from "@chakra-ui/react";
import React, { useState } from "react";

const MapControlButton = (props) => {
  const [active, setActive] = useState(false);

  const onClick = () => {
    setActive(!active);

    props.onClick();
  };

  if (active === true) {
    return (
      <Button
        height="6"
        fontSize="sm"
        bg="black"
        color="white"
        _hover={{ bg: "gray" }}
        onClick={onClick}
      >
        {props.children}
      </Button>
    );
  } else {
    return (
      <Button height="6" fontSize="sm" onClick={onClick}>
        {props.children}
      </Button>
    );
  }
};

export default MapControlButton;
