import { useState, useEffect } from "react";
import { csv } from "d3";

const csvUrl =
  "https://gist.githubusercontent.com/Nfuu/c4c4fa01b7a10184d22a15eeeba4920b/raw/port_country_mapping.csv";

export const useCountryMapping = () => {
  const [data, setData] = useState(null);

  useEffect(() => {
    csv(csvUrl).then(setData);
  }, []);

  return data;
};
