import React, { useState } from "react";
import "./App.css";
import Map from "./components/Map";
import "./styles/custom.css";
import "./styles/outerContainer.css";
import { Box, Center, Divider, VStack } from "@chakra-ui/react";
import PortLinkSidebar from "./components/PortLinkSidebar";
import YearSelector from "./components/YearSelector";
import CommoditySelector from "./components/CommoditySelector";
import MtDisplay from "./components/MtDisplay";
import CO2Display from "./components/CO2Display";
import LevelSelector from "./components/LevelSelector";
import PortDisplay from "./components/PortDisplay";
import MinePlantSidebar from "./components/MinePlantSidebar";
import Tour from "reactour";

const steps = [
  {
    selector: ".css-h0g1du",
    content:
      "This panel shows the coal imported or exported into or out of a chosen port.",
    style: {
      backgroundColor: "#212b57",
      color: "white",
    },
  },
  {
    selector: ".css-kaif03",
    content:
      "Use these controls to select year, commodity and level (country or port). The map shows coal trade flows based on your selection.",
    style: {
      backgroundColor: "#212b57",
      color: "white",
    },
  },
  {
    selector: ".css-1eyjmbc",
    content:
      "When a port or country is selected, this panel shows the closest mines for export installations and closest coal power plants for import installations.",
    style: {
      backgroundColor: "#212b57",
      color: "white",
    },
  },
];

function App() {
  const [isTourOpen, setIsTourOpen] = useState(true);
  const [clickedPort, setClickedPort] = useState({
    Commodity: "Thermal",
    Port: "Newcastle",
    Country: "Australia",
    Year: "2020",
    flow: "130.296205",
    lat: "-32.88979846221539",
    lon: "151.76871572604225",
  });
  const [yearSelected, setYear] = useState(2020);
  const [commoditySelected, setCommodity] = useState("Thermal");
  const [levelSelected, setLevel] = useState("Ports");
  const [portData, setPortData] = useState(null);
  const [flowData, setFlowData] = useState(null);
  const [isPortClicked, setIsPortClicked] = useState("FALSE");

  const getActivePort = () => {
    if (!portData && isPortClicked === "FALSE") {
      return null;
    }

    const mtData = portData.filter(
      (port) =>
        port.Port === clickedPort.Port &&
        +port.Year === yearSelected &&
        port.Commodity === commoditySelected
    );

    try {
      return mtData[0].flow;
    } catch (error) {
      return null;
    }
  };

  return (
    <>
      <div className="outerContainer">
        <Box
          height="100%"
          width="100%"
          maxHeight="1125px"
          maxWidth="2000px"
          backgroundColor="#f0f0f0"
          display="flex"
          flexDirection="column"
          flexWrap="nowrap"
          gridRowGap="15px"
          justifyContent="space-between"
        >
          <Box
            flex="0 0 105px"
            order="1"
            width="100%"
            maxWidth="2000px"
            backgroundColor="#f0f0f0"
            display="flex"
            flexFlow="row wrap"
            justifyContent="space-between"
            alignItems="stretch"
            alignContent="space-between"
            gridColumnGap="15px"
            gridRowGap="15px"
          >
            <Box height="105px" flex="1 1 210px">
              <Box
                width="100%"
                height="100%"
                bg="white"
                borderRadius="lg"
                borderWidth="5pt"
                borderColor={clickedPort.flow > 0 ? "tomato" : "#38B2AC"}
                color="black"
                shadow="2xl"
                alignItems="center"
                justifyContent="center"
                display="flex"
                padding="2"
              >
                <PortDisplay clickedPort={clickedPort} level={levelSelected} />
              </Box>
            </Box>

            <Box height="105px" flex="1 1 190px">
              <Box
                width="100%"
                height="100%"
                bg="white"
                borderRadius="lg"
                color="black"
                shadow="2xl"
                alignItems="center"
                justifyContent="center"
                display="flex"
                position="relative"
                padding="2"
              >
                <MtDisplay mtAmount={getActivePort()} />
              </Box>
            </Box>

            <Box height="105px" flex="1 1 190px">
              <Box
                width="100%"
                height="100%"
                bg="white"
                borderRadius="lg"
                color="black"
                shadow="2xl"
                alignItems="center"
                justifyContent="center"
                display="flex"
                position="relative"
                padding="2"
              >
                <CO2Display mtAmount={getActivePort()} />
              </Box>
            </Box>

            <Box className="selectorItem" height="105px" flex="1 1 475px">
              <Box
                width="100%"
                height="100%"
                bg="white"
                borderRadius="lg"
                color="black"
                shadow="2xl"
                alignItems="center"
                overflow="hidden"
                display="flex"
                padding="2"
              >
                <Box
                  width="40%"
                  height="100%"
                  alignItems="center"
                  justifyContent="center"
                  display="flex"
                >
                  <YearSelector onSelectYear={(value) => setYear(value)} />
                </Box>
                <Center height="50px">
                  <Divider orientation="vertical" />
                </Center>
                <Box
                  width="60%"
                  height="100%"
                  alignItems="center"
                  justifyContent="center"
                  display="flex"
                >
                  <VStack spacing={2}>
                    <CommoditySelector
                      onSelectCommodity={(value) => setCommodity(value)}
                    />
                    <Divider orientation="horizontal" />
                    <LevelSelector
                      onSelectLevel={(value) => setLevel(value)}
                      isPortClicked={(value) => setIsPortClicked(value)}
                    />
                  </VStack>
                </Box>
              </Box>
            </Box>
          </Box>

          <Box
            height="calc(100% - 120px)"
            order="2"
            width="100%"
            maxHeight="1010px"
            maxWidth="2000px"
            backgroundColor="#f0f0f0"
            display="flex"
            flexFlow="row wrap"
            justifyContent="space-between"
            alignItems="stretch"
            alignContent="space-between"
            gridColumnGap="15px"
            gridRowGap="15px"
          >
            <Box order="1" height="100%" flex="0 0 290px">
              <Box
                width="100%"
                height="100%"
                bg="white"
                borderRadius="lg"
                color="black"
                shadow="2xl"
                alignItems="center"
                justifyContent="center"
                padding="5"
                display="flex"
                position="relative"
                overflowY="scroll"
              >
                <PortLinkSidebar
                  clickedPort={clickedPort}
                  portData={portData}
                  flowData={flowData}
                  yearSelected={yearSelected}
                  commoditySelected={commoditySelected}
                  levelSelected={levelSelected}
                  setNewPort={(port) => setClickedPort(port)}
                />
              </Box>
            </Box>

            <Box order="2" height="100%" flex="5 1 400px">
              <Box
                width="100%"
                height="100%"
                bg="white"
                borderRadius="2xl"
                color="black"
                shadow="2xl"
                alignItems="center"
                overflow="hidden"
                position="relative"
              >
                <Map
                  onSelectPort={(object) => setClickedPort(object)}
                  yearSelected={yearSelected}
                  commoditySelected={commoditySelected}
                  levelSelected={levelSelected}
                  setPortData={(object) => setPortData(object)}
                  setFlowData={(object) => setFlowData(object)}
                  isPortClicked={(value) => setIsPortClicked(value)}
                />
              </Box>
            </Box>

            <Box order="3" flex="0 0 290px">
              <Box
                width="100%"
                height="100%"
                bg="white"
                borderRadius="lg"
                color="black"
                shadow="2xl"
                alignItems="center"
                justifyContent="center"
                padding="5"
                display="flex"
                overflowY="scroll"
              >
                <MinePlantSidebar
                  clickedPort={clickedPort}
                  portData={portData}
                  flowData={flowData}
                  yearSelected={yearSelected}
                  commoditySelected={commoditySelected}
                  levelSelected={levelSelected}
                />
              </Box>
            </Box>
          </Box>
        </Box>
        <Tour
          steps={steps}
          isOpen={isTourOpen}
          onRequestClose={() => setIsTourOpen(false)}
        />
      </div>
    </>
  );
}

export default App;
