import React from "react";
import { useCoalPlants } from "../loaders/useCoalPlants";
import {
  BarChart,
  Bar,
  ResponsiveContainer,
  YAxis,
  XAxis,
  LabelList,
  Cell,
} from "recharts";
import { Heading, Box, Spinner, HStack, IconButton } from "@chakra-ui/react";
import { ChevronRightIcon } from "@chakra-ui/icons";
import CustomLabel from "./CustomLabel.js";

const getDistance = (lat1, lon1, lat2, lon2) => {
  var R = 6371; // Radius of the earth in km
  var dLat = deg2rad(lat2 - lat1); // deg2rad below
  var dLon = deg2rad(lon2 - lon1);
  var a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(deg2rad(lat1)) *
      Math.cos(deg2rad(lat2)) *
      Math.sin(dLon / 2) *
      Math.sin(dLon / 2);
  var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  var d = R * c; // Distance in km
  return d;
};

const deg2rad = (deg) => {
  return deg * (Math.PI / 180);
};

const PlantDisplay = (props) => {
  const plants = useCoalPlants();

  if (!plants) {
    return (
      <pre>
        <Spinner color="#38B2AC" />
      </pre>
    );
  }

  const port = props.sourcePort;

  plants.forEach((plant) => {
    plant.Distance = getDistance(
      port.lat,
      port.lon,
      plant.Latitude,
      plant.Longitude
    );

    plant.Capacity = parseFloat(plant.Capacity);
  });

  plants.sort((a, b) => parseFloat(a.Distance) - parseFloat(b.Distance));

  const topPlants = plants.slice(0, 10);

  const handleClick = (data, index) => {
    props.selectPlant(data);
  };

  return (
    <>
      <Box height="100%" width="100%">
        <HStack>
          <Heading size="sm"> Closest coal power plants </Heading>
          <IconButton
            size="xs"
            aria-label="PlantRightButton"
            icon={<ChevronRightIcon />}
            onClick={() => props.selectPlantType("steel")}
          />
        </HStack>
        <ResponsiveContainer width="100%" height="90%">
          <BarChart
            data={topPlants}
            layout="vertical"
            margin={{
              top: 10,
              right: 30,
              left: 120,
              bottom: 5,
            }}
          >
            <XAxis type="number" hide />
            <YAxis type="category" hide />
            <Bar dataKey="Capacity" onClick={handleClick}>
              {topPlants.map((entry, index) => (
                <Cell cursor="pointer" fill="#212b57" key={`cell-${index}`} />
              ))}
              <LabelList
                dataKey="Capacity"
                position="right"
                style={{
                  fontSize: "0.6em",
                  fill: "#000",
                }}
              />
              <LabelList
                dataKey="Name"
                position="left"
                content={<CustomLabel />}
                style={{ fontSize: "0.6em" }}
              />
            </Bar>
          </BarChart>
        </ResponsiveContainer>
      </Box>
    </>
  );
};

export default PlantDisplay;
