import { PieChart, Pie, Cell, ResponsiveContainer } from "recharts";
import { Heading, Box, Center, List, ListItem, Icon } from "@chakra-ui/react";

import "../styles/legend.css";

const COLORS = ["gray", "brown", "black"];

const PortInfo = (props) => {
  const portData = props.portData;

  if (!portData || !props.selectedPort || !props.year) {
    return null;
  }

  const filterData = portData.filter(
    (port) => +port.Year === props.year && port.Port === props.selectedPort
  );

  const data = filterData.map((obj) => ({ ...obj }));

  data.forEach((port) => {
    port.flow = Math.abs(+port.flow);
  });

  const getFlowCommodity = (commodity) => {
    const commodityFiltered = data.filter(
      (port) => port.Commodity === commodity
    );

    try {
      const flow = commodityFiltered[0].flow;
      return flow;
    } catch (error) {
      return null;
    }
  };

  const commodityData = [
    {
      Commodity: "Metallurgical",
      flow: getFlowCommodity("Metallurgical"),
    },
    {
      Commodity: "Thermal",
      flow: getFlowCommodity("Thermal"),
    },
    {
      Commodity: "Unspecified",
      flow:
        getFlowCommodity("Total") -
        (getFlowCommodity("Metallurgical") + getFlowCommodity("Thermal")),
    },
  ];

  return (
    <>
      <Box
        height="100%"
        width="100%"
        justifyContent="center"
        alignItems="center"
      >
        <Heading size="sm">{`${props.selectedPort} commodity breakdown`}</Heading>
        <ResponsiveContainer height={130} width="100%">
          <PieChart height={130}>
            <Pie
              data={commodityData}
              innerRadius={20}
              outerRadius={50}
              dataKey="flow"
              nameKey="Commodity"
            >
              {commodityData.map((entry, index) => (
                <Cell
                  key={`cell-${index}`}
                  fill={COLORS[index % COLORS.length]}
                />
              ))}
            </Pie>
          </PieChart>
        </ResponsiveContainer>
        <Center>
          <List>
            <ListItem fontSize="sm">
              <Icon viewBox="0 0 200 200" color="gray">
                <path
                  fill="currentColor"
                  d="M 100, 100 m -75, 0 a 75,75 0 1,0 150,0 a 75,75 0 1,0 -150,0"
                />
              </Icon>
              Metallurgical coal
            </ListItem>
            <ListItem fontSize="sm">
              <Icon viewBox="0 0 200 200" color="brown">
                <path
                  fill="currentColor"
                  d="M 100, 100 m -75, 0 a 75,75 0 1,0 150,0 a 75,75 0 1,0 -150,0"
                />
              </Icon>
              Thermal coal
            </ListItem>
            <ListItem fontSize="sm">
              <Icon viewBox="0 0 200 200" color="black">
                <path
                  fill="currentColor"
                  d="M 100, 100 m -75, 0 a 75,75 0 1,0 150,0 a 75,75 0 1,0 -150,0"
                />
              </Icon>
              Unspecified coal
            </ListItem>
          </List>
        </Center>
      </Box>
    </>
  );
};

export default PortInfo;
