import React, { useState } from "react";
import {
  BarChart,
  Bar,
  ResponsiveContainer,
  YAxis,
  XAxis,
  LabelList,
  Cell,
} from "recharts";
import { Box, Heading } from "@chakra-ui/react";
import CustomLabel from "./CustomLabel.js";
import CustomLabelCountry from "./CustomLabelCountry.js";

const formatFlowNumber = (num) => {
  var number = parseFloat(num.flow);
  var decimals = number.toFixed(2);

  return parseFloat(decimals);
};

const ExportPortGraph = (props) => {
  const flowData = props.flowData;
  const portData = props.portData;
  const [activeIndex, setActiveIndex] = useState(null);

  if (!flowData || !portData) {
    return null;
  }

  const flows = flowData.filter(
    (flow) =>
      +flow.Year === props.year &&
      flow.Commodity === props.commodity &&
      flow.target === props.targetPort
  );

  flows.sort((a, b) => parseFloat(b.flow) - parseFloat(a.flow));

  flows.forEach((flow) => {
    flow.flow = formatFlowNumber(flow);
  });

  const handleClick = (data, index) => {
    const clickedPortInfo = portData.filter(
      (port) =>
        +port.Year === props.year &&
        port.Commodity === props.commodity &&
        port.Port === data.source
    );

    props.setNewPort(clickedPortInfo[0]);
    setActiveIndex(index);
  };

  return (
    <>
      <Box height="100%" width="100%">
        <Heading size="sm">{`${props.commodity} coal imports (in Mts)`}</Heading>
        <ResponsiveContainer width="100%" height="90%">
          <BarChart
            data={flows.slice(0, 10)}
            layout="vertical"
            margin={{
              top: 10,
              right: 30,
              left: 120,
              bottom: 5,
            }}
          >
            <XAxis type="number" hide />
            <YAxis type="category" hide />
            <Bar dataKey="flow" onClick={handleClick}>
              {flows.slice(0, 10).map((entry, index) => (
                <Cell
                  cursor="pointer"
                  fill={index === activeIndex ? "gray" : "tomato"}
                  key={`cell-${index}`}
                />
              ))}
              <LabelList
                dataKey="flow"
                position="right"
                style={{
                  fontSize: "0.6em",
                  fill: "#000",
                }}
              />
              <LabelList
                dataKey="source"
                position="left"
                content={
                  props.level === "Ports" ? (
                    <CustomLabelCountry />
                  ) : (
                    <CustomLabel />
                  )
                }
                style={{ fontSize: "0.6em" }}
              />
            </Bar>
          </BarChart>
        </ResponsiveContainer>
      </Box>
    </>
  );
};

export default ExportPortGraph;
