import React, { useState } from "react";
import { Box, VStack } from "@chakra-ui/react";
import MineDisplay from "./MineDisplay.js";
import PlantDisplay from "./PlantDisplay.js";
import SteelPlantDisplay from "./SteelPlantDisplay.js";
import MineInfo from "./MineInfo.js";
import PlantInfo from "./PlantInfo.js";
import SteelPlantInfo from "./SteelPlantInfo.js";

const MinePlantSidebar = (props) => {
  const [mine, setMine] = useState(null);
  const [plant, setPlant] = useState(null);
  const [plantMenu, setPlantMenu] = useState("coal");

  const selectedPort = props.clickedPort;

  if (+selectedPort.flow > 0) {
    var flowDirection = "Export";
  } else {
    flowDirection = "Import";
  }

  if (flowDirection === "Export") {
    return (
      <>
        <VStack height="100%" width="100%" spacing="20px">
          <Box height="calc(50% - 10px)" minHeight="240px" width="100%">
            <MineDisplay
              sourcePort={props.clickedPort}
              commodity={props.commoditySelected}
              selectMine={(object) => setMine(object)}
              level={props.levelSelected}
            />
          </Box>
          <Box height="calc(50%)" width="100%">
            <MineInfo mineInfo={mine} />
          </Box>
        </VStack>
      </>
    );
  } else {
    if (plantMenu === "coal") {
      return (
        <>
          <VStack height="100%" width="100%">
            <Box height="calc(50% - 10px)" minHeight="240px" width="100%">
              <PlantDisplay
                sourcePort={props.clickedPort}
                selectPlant={(object) => setPlant(object)}
                level={props.levelSelected}
                selectPlantType={(value) => setPlantMenu(value)}
              />
            </Box>
            <Box height="calc(50%)" width="100%">
              <PlantInfo plantInfo={plant} />
            </Box>
          </VStack>
        </>
      );
    } else {
      return (
        <>
          <VStack height="100%" width="100%">
            <Box height="calc(50% - 10px)" width="100%">
              <SteelPlantDisplay
                sourcePort={props.clickedPort}
                selectPlant={(object) => setPlant(object)}
                level={props.levelSelected}
                selectPlantType={(value) => setPlantMenu(value)}
              />
            </Box>
            <Box height="calc(50%)" width="100%">
              <SteelPlantInfo plantInfo={plant} />
            </Box>
          </VStack>
        </>
      );
    }
  }
};

export default MinePlantSidebar;
