import React from "react";
import { VStack, Badge, Flex, Text } from "@chakra-ui/react";

const PortDisplay = (props) => {
  const selectedPort = props.clickedPort;
  const portName = selectedPort.Port;

  if (+selectedPort.flow > 0) {
    var flowDirection = "Export";
    var badgeColor = "tomato";
  } else {
    flowDirection = "Import";
    badgeColor = "#38B2AC";
  }

  return (
    <>
      <Flex display="flex" alignItems="center">
        <VStack>
          <Text
            fontSize="xl"
            fontWeight="semibold"
            color="gray.600"
            textAlign="center"
          >
            {props.level === "Ports"
              ? `${portName}, ${selectedPort.Country}`
              : `${portName}`}
          </Text>
          <Badge borderRadius="full" px="2" bgColor={badgeColor} color="white">
            {flowDirection}
          </Badge>
        </VStack>
      </Flex>
    </>
  );
};

export default PortDisplay;
