import { Center, Stat, StatLabel, StatNumber, Tooltip } from "@chakra-ui/react";
import { InfoOutlineIcon } from "@chakra-ui/icons";

const CO2Display = (props) => {
  const CO2Amount = Math.abs((+props.mtAmount * 2.44).toFixed(2));

  return (
    <>
      <Center>
        <Stat>
          <StatLabel color="gray.600">CO2 emissions from use:</StatLabel>
          <StatNumber>{`${CO2Amount} Mt CO2`}</StatNumber>
        </Stat>
      </Center>
      <Tooltip
        label="CO2 emissions are calculated by multiplying the megatonnes of coal exported by a factor of 2.44"
        placement="right"
        bgColor="#212b57"
      >
        <InfoOutlineIcon position="absolute" top="10px" right="10px" />
      </Tooltip>
    </>
  );
};

export default CO2Display;
