import { Text } from "recharts";
import { useCountryMapping } from "../loaders/useCountryMapping.js";

const CustomLabelCountry = (props) => {
  const countryMapping = useCountryMapping();

  if (!countryMapping) {
    return null;
  }

  const getCountry = () => {
    var port = countryMapping.filter((port) => port.Port === props.value);

    return port[0].Country;
  };

  return (
    <>
      <Text
        fontSize="0.6em"
        x={props.x - 5}
        y={props.y + props.height / 2}
        width={95}
        textAnchor="end"
        verticalAnchor="middle"
        dominantBaseline="middle"
      >
        {props.value}
        {` ${getCountry()}`}
      </Text>
    </>
  );
};

export default CustomLabelCountry;
