import { Center, Stat, StatLabel, StatNumber, Tooltip } from "@chakra-ui/react";
import { InfoOutlineIcon } from "@chakra-ui/icons";

const MtDisplay = (props) => {
  const mtAmount = +props.mtAmount;
  const displayAmount = Math.abs(mtAmount.toFixed(2));

  if (mtAmount > 0) {
    var displayText = "Exports in megatonnes:";
  } else {
    displayText = "Imports in megatonnes:";
  }

  return (
    <>
      <Center>
        <Stat>
          <StatLabel color="gray.600">{displayText}</StatLabel>
          <StatNumber>{`${displayAmount} Mt`}</StatNumber>
        </Stat>
      </Center>
      <Tooltip
        label="True exports for any given port are likely to be higher. These exports only represent exports from installations that are included in the dataset. We are continuously adding more ports to reduce the amount of exports to unknown ports and provide a more accurate picture."
        placement="right"
        bgColor="#212b57"
      >
        <InfoOutlineIcon position="absolute" top="10px" right="10px" />
      </Tooltip>
    </>
  );
};

export default MtDisplay;
